<template>
  <div class="audio-lizi">
    <canvas id="liziCanvas" width="100%" height="100%"></canvas>
  </div>
</template>

<script>
import MyCanvas from './index.js'
export default {
  props: ['stream'],
  data() {
    return {
      myCanvas: ''
    }
  },
  watch: {
    stream(val) {
      this.myCanvas = new MyCanvas('liziCanvas', { stream: this.stream })
    }
  },
  mounted() {
    // setTimeout(() => { this.start() }, 0)
  },
  methods: {
    start() {
      
      console.log('init', this,this.myCanvas)
    },
    play() {
      console.log('start')
      this.myCanvas.start()
    },
    pause() {
      console.log('stop')
      this.myCanvas.stop()
    }
  }
}
</script>

<style>
.audio-lizi {
  width: 100%;
  height: 600px;
}
#liziCanvas
{
    width: 100%;
    height: 100%;
}
</style>