<template>
  <div>
    <h1 style="text-align: center;">视频聊天室</h1>
    <h3>输入同样的房间名称即可发起聊天</h3>
    <input :readonly="roomReadonly" :class="roomReadonly?'is-disabled':''" type="text" v-model="roomid" placeholder="输入房间名称" id="roomid" />
    <div class="row1">
      <button id="connect" class="btn" :class="btnEls.connect.disabled?'is-disabled':''" @click="joinRoom">加入</button>
      <button id="disconnect" class="btn" :class="btnEls.disconnect.disabled?'is-disabled':''" @click="closeWS" disabled>离开</button>
    </div>
    <div class="row2">
      <button id="getAudio" class="btn launchBtn" :class="btnEls.getAudio.disabled?'is-disabled':''" @click="launchChat('audio')" disabled>发起语音聊天</button>
      <button id="getVideo" class="btn launchBtn" :class="btnEls.getVideo.disabled?'is-disabled':''" @click="launchChat('video')" disabled>发起视频聊天</button>
      <button id="getScreen" class="btn launchBtn" :class="btnEls.getScreen.disabled?'is-disabled':''" @click="launchChat('screenshare')" disabled>发起屏幕共享</button>
    </div>
    <div class="row3">
      <button id="closeChat" class="btn" :class="btnEls.closeChat.disabled?'is-disabled':''" @click="closeChat" disabled>关闭当前通话</button>
    </div>
    <div class="logbox">
      <p><span>日志</span><span style="cursor:pointer;" @click="logshow=!logshow">{{logshow?'收起':'展开'}}</span></p>
      <textarea readonly v-model="logVal" id="logs" :class="logshow?'logshow':''"></textarea>
    </div>
    <!-- 语音播放 -->
    <!-- <div class="audiobox" v-show="subType==='audio'"> -->
    <div class="audiobox">
      <audio style="display:none;" muted src="" controls id="audio"></audio>
      <LiZi ref="audioLizi" :stream="audioStream" />
    </div>
    <!-- 视频播放 -->
    <div class="videobox" v-show="subType==='video'">
      <div class="localbox" style="margin-bottom: 20px;">
        <div class="video-wrap">
          <video muted src id="localVideo"></video>
        </div>
      </div>
      <div class="remotebox">
        <div class="video-wrap">
          <video src id="remoteVideo"></video>
        </div>
      </div>
    </div>
    <!-- 屏幕共享 -->
    <div class="screenshare" v-show="subType==='screenshare'">
      <video src id="screenVideo" controls></video>
    </div>
    <div class="message-box__wrapper" v-show="inviteDialog" style="z-index: 2010;">
      <div class="message-box">
        <div class="message-box__message">
          <p>是否接受{{inviteMsg}}</p>
        </div>
        <div class="message-box__btns">
          <button type="button" class="btn" @click="cancelInvite">取消</button>
          <button type="button" class="btn primary-btn" @click="sureInvite">接受</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LiZi from '@/components/lizi'
export default {
  components: { LiZi },
  data() {
    return {
      logshow: true,
      roomid: '',
      roomPerson: 0,
      logVal: '',
      user: '',
      subType: '',
      isWsConnect: false,
      isChat: false,
      roomReadonly: false,
      ws: '',
      serverConfig: {
        iceServers: [{ urls: ['stun:stun.xten.com'] }]
      },
      audio: '',
      localVideo: '',
      localVideoShow: false,
      remoteVideo: '',
      remoteVideoShow: false,
      remoteAudioShow: false,
      screenVideo: '',
      rtcPC: '',
      btnEls: {
        getAudio: '',
        getVideo: '',
        getScreen: '',
        connect: '',
        disconnect: '',
        closeChat: ''
      },
      inviteMsg: '',
      inviteDialog: false,
      audioStream: {}
    }
  },
  watch: {
    isWsConnect(val, oldVal) {
      if (val) {
        this.roomReadonly = true
        this.btnEls.connect.disabled = true
        this.btnEls.disconnect.disabled = false
      } else {
        this.roomReadonly = false
        this.btnEls.connect.disabled = false
        this.btnEls.disconnect.disabled = true
        this.btnEls.getAudio.disabled = true
        this.btnEls.getVideo.disabled = true
        this.btnEls.getScreen.disabled = true
      }
    },
    isChat(val) {
      if (val) {
        this.btnEls.closeChat.disabled = false
        this.btnEls.getAudio.disabled = true
        this.btnEls.getVideo.disabled = true
        this.btnEls.getScreen.disabled = true
      } else {
        this.btnEls.closeChat.disabled = true
        if (this.isWsConnect) {
          this.btnEls.getVideo.disabled = false
          this.btnEls.getScreen.disabled = false
          this.btnEls.getAudio.disabled = false
        }
      }
    },
    roomPerson(val) {
      if (val == 2) {
        this.btnEls.getAudio.disabled = false
        this.btnEls.getVideo.disabled = false
        this.btnEls.getScreen.disabled = false
      } else {
        this.btnEls.getAudio.disabled = true
        this.btnEls.getVideo.disabled = true
        this.btnEls.getScreen.disabled = true
      }
    }
  },
  mounted() {
    this.initRtcPC()
    this.localVideo = document.getElementById('localVideo')
    this.remoteVideo = document.getElementById('remoteVideo')
    this.screenVideo = document.getElementById('screenVideo')
    this.audio = document.querySelector('audio')
    this.audio.onpause = () => { this.$refs.audioLizi.pause() }
    this.audio.onplay = () => { this.$refs.audioLizi.play() }
    this.remoteVideo.onloadeddata = () => { this.remoteVideo.play() }
    this.localVideo.onloadeddata = () => { this.localVideo.play() }
    this.screenVideo.onloadeddata = () => { this.screenVideo.play() }
    this.btnEls = {
      getAudio: document.getElementById('getAudio'),
      getVideo: document.getElementById('getVideo'),
      getScreen: document.getElementById('getScreen'),
      connect: document.getElementById('connect'),
      disconnect: document.getElementById('disconnect'),
      closeChat: document.getElementById('closeChat')
    }
  },
  methods: {
    // 挂断当前通话
    closeChat() {
      this.closeRtcPC()
      this.updateLog('关闭聊天')
      this.isChat = false
      this.wsSendMessage('closePhone', '挂掉了电话')
      if (this.subType == 'audio') {
        this.audio.pause()
      } else if (this.subType == 'video') {
        this.localVideo.pause()
        this.remoteVideo.pause()
      } else if (this.subType == 'screenshare') {
        this.screenVideo.pause()
      }
      this.subType = ''
    },
    handleClosePhone() {
      if (this.isChat) {
        this.closeChat()
      }
    },
    // 初始化rtc
    initRtcPC() {
      console.log('init rtc')
      this.closeRtcPC()
      this.rtcPC = new RTCPeerConnection(this.serverConfig)
      this.rtcPC.addEventListener('icecandidate', e => this.onIceCandidate(e))
      this.rtcPC.addEventListener('iceconnectionstatechange', e => this.onIceStateChange(this.rtcPC, e))
      this.rtcPC.onaddstream = function(e) { console.log('addStream') }
      // 监听流加入音视频轨道
      var that = this
      this.rtcPC.ontrack = function(e) {
        console.log('ontrack')
        that.remoteStream = e.streams
        if (that.subType === 'audio') {
          if (that.audio.srcObject != e.streams[0]) {
            that.audio.srcObject = e.streams[0]
            that.audio.pause()
            that.audio.play()
          }
        } else if (that.subType === 'video') {
          if (that.remoteVideo.srcObject != e.streams[0]) {
            that.remoteVideo.srcObject = e.streams[0]
          }
        } else if (that.subType === 'screenshare') {
          if (that.screenVideo.srcObject != e.streams[0]) {
            that.screenVideo.srcObject = e.streams[0]
          }
        }
      }
    },
    // closertc
    closeRtcPC() {
      if (this.rtcPC) {
        console.log('close rtc')
        this.rtcPC.getLocalStreams().forEach(stream => {
          stream.getTracks().forEach(track => track.stop())
          console.log('getLocalStreams and stop', stream)
          this.rtcPC.removeStream(stream)
        })
        this.rtcPC.getRemoteStreams().forEach(stream => {
          stream.getTracks().forEach(track => track.stop())
          console.log('getRemoteStreams stop', stream)
          this.rtcPC.removeStream(stream)
        })
        this.rtcPC.close()
        this.rtcPC = ''
      }
    },
    // 更新日志
    updateLog(msg, other) {
      this.logVal += other ? other : '' + ' ' + msg + '\n'
    },
    // 点击加入房间
    joinRoom() {
      if (this.roomid == '') {
        alert('不能为空')
        return
      }
      this.connectWs()
    },
    // 加入房间成功
    joinSuccess(res) {
      this.user = res.user
      this.isWsConnect = true
    },
    // 连接websocket
    connectWs() {
      // this.ws = new WebSocket('wss://127.0.0.1:8001')
      this.ws = new WebSocket('wss://txc.wlin.club:8001')
      this.ws.onopen = e => {
        this.wsSendMessage('join', '大家好')
      }
      this.ws.onclose = e => {
        this.updateLog('离开房间,连接关闭')
        this.isWsConnect = false
      }
      this.ws.onerror = e => {
        this.updateLog('连接出错', JSON.stringify(e))
        this.isWsConnect = false
      }
      this.ws.onmessage = e => {
        var res = ''
        try {
          res = JSON.parse(e.data)
        } catch {
          res = e.data
        }
        this.handleWsMessage(res)
      }
    },
    // 发送消息
    async wsSendMessage(type, msg) {
      msg && typeof msg == 'object' ? (msg = JSON.stringify(msg)) : ''
      if (type == 'invite') console.log('sendmessage inite msg', msg)
      await this.ws.send(JSON.stringify({ roomid: this.roomid, user: this.user, type, subType: this.subType, msg }))
    },
    // 处理ws返回的消息
    handleWsMessage(res) {
      switch (res.type) {
        case 'broadcast': this.judgePeople(res.msg);this.updateLog(res.msg); break;
        case 'invite': this.handleInvite(res); break;
        case 'acceptInvite': this.acceptInvite(res); break;
        case 'refuseInvite': this.refuseInvite(res); break;
        case 'offer': this.recOffer(res); break;
        case 'answer': this.getResponse(res.msg); break;
        case 'candidate': this.addIceCandidate(res.msg); break;
        case 'closePhone': this.handleClosePhone(res.msg); break;
        // case 'noOffer': this.openLocalDevice(); break;
        // case 'hasOffer': document.getElementById('getVideo').disabled = true; break;
        case 'joinSuccess': this.joinSuccess(res); break;
        case 'joinFail': this.isWsConnect = false;this.updateLog(res.msg); break;
        default: this.updateLog(res.msg, '对方');
      }
    },
    judgePeople(msg) {
      var res = msg.match(/当前房间有(\d)人/)
      if (res && res[1]) this.roomPerson = res[1] - 0
    },
    judgeState() {
      if (this.roomid === '') {
        alert('请先加入房间')
        return false
      }
      return true
    },
    // 向对方发出聊天邀请
    launchChat(subType) {
      if (!this.judgeState()) return
      this.closeRtcPC()
      this.subType = subType
      var msg = ''
      if (subType === 'audio') {
        this.updateLog('向对方发出语音聊天邀请')
        msg = '对方邀请您语音聊天'
      } else if (subType === 'video') {
        this.updateLog('向对方发出视频聊天邀请')
        msg = '对方邀请您视频聊天'
      } else if (subType === 'screenshare') {
        this.updateLog('向对方发出观看屏幕共享邀请')
        msg = '对方邀请您观看屏幕共享'
      }
      this.wsSendMessage('invite', msg)
    },
    // 处理对方发来的邀请
    handleInvite(res) {
      this.updateLog(res.msg)
      console.log('handleInvie, msg', res.msg)
      this.subType = res.subType
      this.inviteMsg = res.msg
      this.inviteDialog = true
    },
    // 取消邀请
    cancelInvite() {
      this.wsSendMessage('refuseInvite', '拒绝邀请')
      this.updateLog('拒绝对方邀请')
      this.inviteDialog = false
      this.isChat = false
    },
    // 接受邀请
    sureInvite() {
      this.initRtcPC()
      this.wsSendMessage('acceptInvite', '接受邀请')
      this.updateLog('接受对方邀请, 请允许打开相关设备')
      this.inviteDialog = false
      this.isChat = true
    },
    // 对方接受邀请
    acceptInvite(res) {
      this.isChat = true
      this.updateLog('对方接受邀请')
      this.initRtcPC()
      this.openLocalDevice(res.subType, 'createOffer')
    },
    // 对方拒绝视频邀请
    refuseInvite(msg) {
      this.isChat = false
      this.updateLog('对方拒绝邀请')
    },
    // 判断打开本地设备
    openLocalDevice(deviceType, cb, param) {
      console.log('openLocalDevice', deviceType)
      if (deviceType === 'audio') {
        this.updateLog('请允许网页打开本地麦克风')
        this.openAudioDevice(cb, param)
      } else if (deviceType === 'video') {
        this.updateLog('请允许网页打开本地摄像头和麦克风')
        this.openVideoDevice(cb, param)
      } else if (deviceType === 'screenshare') {
        if (cb === 'createAnswer') {
          this.createAnswer(param)
        } else {
          this.updateLog('请选择共享窗口')
          this.openDisplayDevice(cb, param)
        }
      }
    },
    // 打开本地麦克风
    openAudioDevice(cb, param) {
      navigator.getUserMedia({ audio: true }, stream => {
        console.log('获取到本地音频流')
        this.audioStream = stream
        stream.getTracks().forEach(track => this.rtcPC.addTrack(track, stream))
        console.log('openAudioDevice, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      }, error => {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      })
    },
    // 打开本地摄像头和麦克风
    openVideoDevice(cb, param) {
      navigator.getUserMedia({ video: true, audio: true }, stream => {
        console.log('获取到本地视频流')
        this.localVideo.srcObject = stream
        stream.getTracks().forEach(track => this.rtcPC.addTrack(track, stream))
        console.log('openVideoDevice, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      }, error => {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      })
    },
    // 选择共享窗口
    async openDisplayDevice(cb, param) {
      try {
        var stream = await navigator.mediaDevices.getDisplayMedia({ video: true, audio: true })
        console.log('获取到本地共享窗口流')
        this.screenVideo.srcObject = stream
        stream.getTracks().forEach(track => this.rtcPC.addTrack(track, stream))
        console.log('openDisplayDevices, add track to pc')
        // createoffer
        if (cb === 'createOffer') {
          this.createOffer()
        } else if (cb === 'createAnswer') {
          this.createAnswer(param)
        }
      } catch (error) {
        console.log('opendevice error', error)
        this.wsSendMessage('other', error)
      }
    },
    // 1.创建发起offer
    createOffer() {
      this.rtcPC.createOffer(offer => {
        this.rtcPC.setLocalDescription(new RTCSessionDescription(offer), () => {
          console.log('createoffer')
          this.wsSendMessage('offer', offer)
        }, error => { console.log('setLocalDescription error', error) })
      }, error => { console.log('createOffer error', error) })
    },
    // 2.创建回答
    createAnswer(offer) {
      this.rtcPC.setRemoteDescription(new RTCSessionDescription(offer), () => {
        this.rtcPC.createAnswer(answer => {
          this.rtcPC.setLocalDescription(new RTCSessionDescription(answer), () => {
            this.wsSendMessage('answer', answer)
          }, error => console.log('setLocalDescription', error))
        }, error => console.log('createAnswer', error))
      }, error => console.log('setRemoteDescription', error))
    },
    // 3.收到回复
    getResponse(answer) {
      console.log('recAnswer', typeof answer)
      answer = JSON.parse(answer)
      this.rtcPC.setRemoteDescription(new RTCSessionDescription(answer), () => {  }, error => {  })
    },
    // 接收到offer
    async recOffer(res) {
      console.log('recoffer', typeof res.msg)
      var offer
      if (typeof res.msg === 'object') {
        offer = res.msg
      } else {
        offer = JSON.parse(res.msg)
      }
      this.openLocalDevice(res.subType, 'createAnswer', offer)
    },
    // 获取到本地IceCandidate
    async onIceCandidate(event) {
      console.log('onIceCandidate')
      try {
        if (event.candidate) {
          this.wsSendMessage('candidate', event.candidate)
        }
      } catch (e) {
        console.log('onAddIceCandidateError', e)
      }
    },
    // 添加对方IceCandidate
    async addIceCandidate(candidate) {
      var rtcIceCandidate
      try {
        candidate = JSON.parse(candidate)
        rtcIceCandidate = new RTCIceCandidate(candidate)
        await this.rtcPC.addIceCandidate(rtcIceCandidate)
      } catch {}
      console.log('reciver candidate and add address',rtcIceCandidate.address)
      if (/^[\d]{1,3}/.test(rtcIceCandidate.address)) {
        this.updateLog('对方address:' + rtcIceCandidate.address)
      }
    },
    onIceStateChange(pc, event) {
      if (pc) {
        console.log(`${pc} ICE state: ${pc.iceConnectionState}`)
        console.log('ICE state change event: ', event)
        if (pc.iceConnectionState === 'disconnected') {
          this.updateLog('对方挂掉了电话')
          if (this.isChat) this.closeChat()
        }
      }
    },
    // 关闭websocket连接
    closeWS() {
      this.rtcPC && this.closeRtcPC()
      if (this.ws) {
        this.wsSendMessage('leave', '对方挂断了电话')
        this.ws.close()
        this.ws = ''
        this.isWsConnect = false
        this.isChat = false
        if (this.roomPerson >=1 ) this.roomPerson--
      }
    }
  },
  beforeDestroy() {
    if (this.isChat) {
      this.wsSendMessage('closePhone', '挂掉了电话')
    }
    this.closeWS()
  }
}
</script>

<style>
h1, h3 {
  color: #ccc;
}
.videobox {
  width: 100%;
  margin-top: 100px;
  /* display: flex; */
  /* justify-content: space-around; */
}
.localbox, .remotebox {
  width: 40%;
  /* height: 300px; */
}
.videobox .video-wrap {
  /* background: #000; */
  height: 100%;
}
.videobox video {
  /* display: none; */
  /* width: 40%; */
}
input {
  margin-bottom: 20px;
}
.row1, .row2, .row3 {
  margin-bottom: 20px;
}
.btn {
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0 20px;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.btn:hover {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
.btn.is-disabled {
  color: #c0c4cc;
  cursor: not-allowed;
  background-image: none;
  background-color: #fff;
  border-color: #ebeef5;
}
#roomid {
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  width: 200px;
}
#roomid.is-disabled {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #c0c4cc;
  cursor: not-allowed;
}
.logbox {
  position: fixed;
  top: 50px;
  right: 50px;
  width: 30%;
  box-sizing: border-box;
}
.logbox p {
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  height: 30px;
  line-height:30px;
  margin: 0;
  background: #ccc;
}
.logbox .logshow {
  height: 300px;
  transition: height 0.5s;
  border-width: 1px;
}
textarea {
  box-sizing: border-box;
  display: block;
  height: 0;
  width: 100%;
  border-width: 0;
  resize: none;
  background: #c6e2ff;
  font-size: 20px;
}
textarea:focus {
  outline: none;
}
textarea:hover {
  outline: none;
}
.message-box__wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: rgba(0,0,0,.5);
}
.message-box__wrapper:after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  vertical-align: middle;
}
.message-box {
  display: inline-block;
  width: 420px;
  padding-bottom: 20px;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #ebeef5;
  font-size: 18px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  text-align: left;
  overflow: hidden;
  backface-visibility: hidden;
}
.message-box__message {
  text-align: center;
  /* color: #ccc; */
  font-size: 20px;
}
.message-box__btns {
  display: flex;
  justify-content: space-around;
}
.primary-btn {
  background-color: #409eff;
  border-color: #409eff;
}
</style>
